"use client"

import { Menu } from "antd"
import { useUserStore } from "../../../shared/providers/userProvider"
import { UserRoleEnum } from "../../../shared/entities/RoleEnum/UserRoleEnum"
import { MenuItems } from "../types/items"
import { useLayoutEffect } from "react"
import { getUserInfo } from "../api"

export const TopBarMenu = () => {
  const { user, setUser } = useUserStore(state => state)

  let MenuItemsRoles = MenuItems.slice(0, 0)

  useLayoutEffect(() => {
    getUserInfo().then(response => {
      const data = response.data
      if ("error" in data) {
        return
      }
      setUser({ ...data })
      MenuItemsRoles = data.role === UserRoleEnum.ADMIN ? MenuItems : MenuItemsRoles
    })
  }, [])

  return (
    <Menu
      mode='horizontal'
      items={MenuItemsRoles}
    />
  )
}
